// Variables
$background-color: rgba(51, 51, 51, 0.05);
$text-color: #333;
$font-size-normal: 1rem;
$font-size-small: 0.9rem;
$font-weight: 400;

.App {
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  color: $text-color;
  font-size: $font-size-normal;
  font-weight: $font-weight;
}

// Login
.signin {
  display: flex;
  flex-direction: column;
  justify-content: center;
  height: 100vh;

  .profile-pic-div {
    display: flex;
    justify-content: center;

    .profile-pic {
      width: 7rem;
      height: 7rem;
    }
  }

  .login-button-div {
    display: flex;
    justify-content: center;
  }
}

// Header

// Main
.main {
  height: 100%;
  width: 100%;

  .event-item {
    border-radius: 0.5rem;
    padding: 0.5rem 1.5rem;

    .event-head * {
      padding: 0.1rem;
    }

    .event-body {
      font-size: $font-size-small;
    }
  }

  .event-item:hover {
    box-shadow: rgba(0, 0, 0, 0.12) 0px 0px 3px, rgba(0, 0, 0, 0.24) 0px 1px 2px;
  }

  .person {
    .profile-pic-div {
      display: flex;
      justify-content: center;

      .profile-pic {
        width: 7rem;
        height: 7rem;
      }
    }

    .update-button-div {
      .save-button,
      .cancel-button {
        margin: 0 1rem;
      }
    }
  }

  .event {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    height: 100%;

    .event-header,
    .event-body,
    .event-footer {
      display: flex;
      flex-direction: column;
      justify-content: space-around;
      align-items: center;
    }

    .event-header {
      height: 8rem;
    }

    .event-footer {
      height: 7rem;
    }
  }
}

// Utility Classes
.hidden {
  display: hidden;
}

// Footer
.footer {
  background: #fff;
}
