.App {
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  color: #333;
  font-size: 1rem;
  font-weight: 400;
}

.signin {
  display: flex;
  flex-direction: column;
  justify-content: center;
  height: 100vh;
}
.signin .profile-pic-div {
  display: flex;
  justify-content: center;
}
.signin .profile-pic-div .profile-pic {
  width: 7rem;
  height: 7rem;
}
.signin .login-button-div {
  display: flex;
  justify-content: center;
}

.main {
  height: 100%;
  width: 100%;
}
.main .event-item {
  border-radius: 0.5rem;
  padding: 0.5rem 1.5rem;
}
.main .event-item .event-head * {
  padding: 0.1rem;
}
.main .event-item .event-body {
  font-size: 0.9rem;
}
.main .event-item:hover {
  box-shadow: rgba(0, 0, 0, 0.12) 0px 0px 3px, rgba(0, 0, 0, 0.24) 0px 1px 2px;
}
.main .person .profile-pic-div {
  display: flex;
  justify-content: center;
}
.main .person .profile-pic-div .profile-pic {
  width: 7rem;
  height: 7rem;
}
.main .person .update-button-div .save-button,
.main .person .update-button-div .cancel-button {
  margin: 0 1rem;
}
.main .event {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 100%;
}
.main .event .event-header,
.main .event .event-body,
.main .event .event-footer {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
}
.main .event .event-header {
  height: 8rem;
}
.main .event .event-footer {
  height: 7rem;
}

.hidden {
  display: hidden;
}

.footer {
  background: #fff;
}/*# sourceMappingURL=App.css.map */